.header {
    position: sticky;
    z-index: 100;
    height: 70px;
    // border-bottom: 1px solid gold;
    top: 0;
    padding: 8px;
    background-color: #333;
    color: white;
    display: flex;
    align-items: center;

    &__nav {
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;

        display: flex;
        row-gap: 24px;
        font-size: 24px;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        background-color: rgba(22, 22, 22, 0.8);
        backdrop-filter: blur(5px);
        transition: transform 0.5s;
        transform: translateY(-100%);

        &.active {
            transform: translateY(0);
        }
    }

    &__link {
        color: white;
        text-decoration: none;

        &.logo {
            display: flex;
            gap: 8px;
        }
    }

    &__logo {
        &-wrapper {
            display: flex;
            align-items: center;
            gap: 8px;
        }

        &-text {
            margin: 0;
            font-size: 32px;
            color: white;
            text-decoration: none;
        }
        &-img {
            animation: spin infinite 20s linear;
            height: 50px;
        }
    }


    &__title {
        margin: 0;
        font-size: 32px;
    }
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}