.all-orders {
    width: 100%;
    min-height: 100vh;
    padding: 0 12px;

    &__title {
        font-size: 32px;
        text-align: center;
    }

    &__table {
        font-size: 14px;
        overflow-x: scroll;
        width: 100%;
        border-collapse: collapse;
        margin: 24px 0;

        & th {
            cursor: pointer;
            user-select: none;
            padding: 8px 0;
            background-color: rgba(255,255,255,0.5);
            border: 1px solid white;
            color: #333;

            &:nth-child(1) {
                width: 40px;
                text-align: center;
            }
        }

        &-row {
            border-bottom: 1px solid grey;
            transition: 0.3s;
            &:nth-child(2n) {
                background-color: rgba(255,255,255,0.12);
            }

            &:hover {
                background-color: rgba(255,255,255,0.8);
                color: rgba(0,0,0,0.8);
            }
        }

        &-cell {
            padding: 12px 4px;
            justify-content: center;
            align-items: center;

            &:nth-child(1) {
                width: 40px;
                text-align: center;
            }
        }
    }
}

.controlls {
    &__box {
        min-height: 40px;
        margin: 10px 0;
        display: grid;
        column-gap: 24px;
        grid-template-columns: repeat(2, 1fr);
        text-align: center;
        &.selected {
            grid-template-columns: repeat(4, 1fr);
        }

        span {
            font-size: 20px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    &__select {
        background-color: white;
        color: #333;
        text-align: center;
    }

    &__button {
        background-color: transparent;
        outline: none;
        img {
            height: 40px;
        }
    }
}

.order-info {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__table {
        width: 100%;
        border: 1px solid white;

        &-row {

            td {
                padding: 4px 8px;
            }

            &:nth-child(2n) {
                background-color: rgba(255,255,255,0.12);
            }
        }
    }
}