.new-order {
    padding: 0 12px;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;

    &__title {
        display: flex;
        justify-content: center;
        align-items: center;
        column-gap: 8px;
        font-size: 28px;
        margin: 12px 0 0;

        &-input {
            background-color: white;
            font-size: 20px;
            width: 60px;
            height: 30px;
        }
    }

    &__form {
        width: 100%;
        text-align: center;
    }

    &__details {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }

    &__phone {
        font-weight: bold;
        font-size: calc(10px + 2vmin);
        display: grid;
        align-items: center;
        grid-template-columns: 1fr 7fr 1fr;

        input {
            padding: 8px 0;
        }
    }

    &__fieldset {
        text-align: left;
        border-radius: 4px;
    }

    &__input {
        background-color: white;
        border-radius: 4px;
        text-align: center;
        width: 100%;
        font-size: 20px;

        &.street {
            width: 100%;
            padding: 8px;
        }
    }

    &__select {
        border-radius: 4px;
        padding: 8px 0;
        text-align: center;
        font-size: 20px;
        width: 100%;
    }

    &__submit {
        cursor: pointer;
        width: 100%;
        margin: 24px 0 0;
        padding: 8px;
        font-size: 20px;
        background-color: transparent;
        color: gold;
        border: 4px solid gold;
        border-radius: 8px;
    }
}