@import '../../styles/main.scss';

.main {
    border: 5px solid lightblue;
    padding: 0 24px;
    height: calc(100vh - 70px);
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: url('./medusa.png') no-repeat center center;
    background-color: rgba(0, 0, 0, 0.7);

    &__title {
        &-wrapper {
            display: flex;
            column-gap: 12px;
            align-items: center;
        }

        &-text {
            margin: 16px 0 ;
            font-size: 7vmin;
            color: lightblue;
        }

        &-icon {
            animation: glowing 3s alternate infinite;
            padding: 16px;
        }
    }

    &__subtitle {
        margin: 24px 0;
        font-size: 3vmin;
    }

    &__nav {
        width: 100%;
        height: calc(100vh - 350px);
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 24px;
        // padding: 0 24px;
    }

    &__link {
        display: grid;
        place-items: center;
        text-align: center;
        backdrop-filter: blur(5px);
        background-color: $color-lightblue-faded12;
        font-size: 4vmin;
        color: white;
        border-radius: 8px;
        text-decoration: none;

        &:hover {
            color: $color-lightblue-filled;
            box-shadow: 0 0 64px 0 rgba(255, 255, 255, 0.24);
            background-color: $color-lightblue-faded24;
        }
    }
}




@keyframes glowing {
    0% {
        filter: drop-shadow(0 0 20px rgba(255, 255, 255, 1));
    }

    25% {
        filter: drop-shadow(0 0 8px rgba(38, 94, 38, 0.7));
    }

    45% {
        filter: drop-shadow(0 0 10px rgba(48, 118, 48, 1));
    }

    65% {
        filter: drop-shadow(0 0 12px rgba(38, 110, 58, 0.7));
    }

    85% {
        filter: drop-shadow(0 0 14px rgba(28, 95, 78, 0.5));
    }

    100% {
        filter: drop-shadow(0 0 24px rgba(255, 255, 255, 1));
    }
}